  }

  // Methods
  private listenOnSampleExampleSelect($event: Event): void {
    this.dataSelectedSampleExampleIndex = Number(<any>$event);
    this.dataText = this.dataSampleExamples[this.dataSelectedSampleExampleIndex].content;
  }
  private listenInput($event: Event): void {
    this.dataText = (<HTMLInputElement>(<any>$event).target).value;
  }
  private calcStatistics(): void {
    this.dataSamples.length = 0;
    let samples: string[] | null = this.dataText.match(/(\+|\-)?([1-9][0-9]+|[0-9])(\.[0-9]+)?/g);
    if (samples !== null) {
      let decimalPartCounts: number[] = [];
      samples.forEach((value: string): void => {
        this.dataSamples.push(Number(value));
        decimalPartCounts.push(MathUtil.countDecimlPart(Number(value)));
      });
      let decimalPartCount: number = StatisticsCalc.calcMaximum(decimalPartCounts);
      this.dataSampleSize = String(MathUtil.toLocaleString(this.dataSamples.length));
      this.dataMean = MathUtil.toFixed(StatisticsCalc.calcMean(this.dataSamples), decimalPartCount + 2);
      let modes: number[] = StatisticsCalc.calcMode(this.dataSamples);
      this.dataMode = modes.length === 0 ? '--' : modes.join(' ');
      this.dataMedian = MathUtil.toFixed(StatisticsCalc.calcMedian(this.dataSamples), decimalPartCount);
      this.dataMaximum = String(StatisticsCalc.calcMaximum(this.dataSamples));
      this.dataMinimum = String(StatisticsCalc.calcMinimum(this.dataSamples));
      this.dataVariance = MathUtil.toFixed(StatisticsCalc.calcVariance(this.dataSamples), decimalPartCount + 2);
      this.dataStandardDeviation = MathUtil.toFixed(StatisticsCalc.calcStandardDeviation(this.dataSamples), decimalPartCount + 2);
      let kSigmaIntervals: {
        _1sigmaInterval: number,
        _2sigmaInterval: number,
        _3sigmaInterval: number,
      } = StatisticsCalc.calcKSigmaIntervals(this.dataSamples);
      this.dataKSigmaIntervals = {
        _1sigmaInterval: MathUtil.toFixed(kSigmaIntervals._1sigmaInterval * 100, 2),
        _2sigmaInterval: MathUtil.toFixed(kSigmaIntervals._2sigmaInterval * 100, 2),
        _3sigmaInterval: MathUtil.toFixed(kSigmaIntervals._3sigmaInterval * 100, 2),
      };
    }
  }
